
import React, { Component, Suspense } from 'react';

const NeedSupport = React.lazy(() => import('../../../CommonComponents/NeedSupport'));
const AddFeedByNetwork = React.lazy(() => import('./Elements/AddFeedByNetwork'));
const Networks = React.lazy(() => import('./Elements/Networks'));
const ChooseCollection = React.lazy(() => import('./Elements/ChooseCollection'));
const AssetsUploader = React.lazy(() => import('./Elements/AssetsUploader'));

class UploadAssets extends Component {

    constructor() {
        super();
        this.state = {uploadTypeSec: 'uploadAssets'};
    }

    uploadType = (uploadTypeVal) => {
        this.setState({
            uploadTypeSec: uploadTypeVal
        })
    }
    getAssets = () => {
        this.props.getAssetsCallback()
    };
    render() {
        const {uploadTypeSec} = this.state;
        return <div className="container container-xs">
                    <div className="block-center flex-column view-sm-h">
                        <div className="addSource d-flex align-items-start flex-column flex-lg-row w-100">
                            <div className="addSourceNetwork d-flex flex-column flex-shrink-0 pe-lg-4">
                                <div className="card mb-xxl-4 mb-3">
                                    <div className="card-body px-2">
                                        <Suspense fallback={<div>Loading...</div>}>
                                            <Networks uploadTypeCallback={this.uploadType} />
                                        </Suspense>
                                    </div>
                                </div>
                            </div>
                            <div className="d-flex flex-column flex-grow-1 w-100">
                                <div className="">
                                    <h6 className="fs-7 mb-3">Select the collection where you want to put your data</h6>
                                    <Suspense fallback={<div>Loading...</div>}>
                                        <ChooseCollection />
                                    </Suspense>

                                    {uploadTypeSec === 'uploadAssets' ?
                                    <Suspense fallback={<div>Loading...</div>}>
                                        <AssetsUploader />
                                    </Suspense>
                                    :
                                    <Suspense fallback={<div>Loading...</div>}>
                                        <AddFeedByNetwork />
                                    </Suspense>
                                    }

                                    <div className="d-flex flex-wrap align-items-center justify-content-between my-4">
                                        <Suspense fallback={<div>Loading...</div>}>
                                            <NeedSupport content={'Need Help'} supportUrl={'#'} videoUrl={'#'} />
                                        </Suspense>
                                        <div className="my-1 d-flex align-items-center justify-content-end flex-wrap">
                                            <button type="button" className="btn btn-secondary me-3">Cancel</button>
                                            <button type="button" className="btn btn-primary" onClick={()=> this.getAssets()}>Fetch Assets</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    }
}

export default UploadAssets;